<template lang="pug">
.text-button(@click="click" :class="{disabled: disabled}")
  slot
</template>

<script>
export default {
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    click() {
      if (!this.disabled) {
        this.$emit('click');
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.text-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;

  background: white;
  border-radius: 4px;
  border: 1px solid black;
  height: 2em;

  user-select: none;
  cursor: pointer;
  &:not(.disabled) {
    &:hover {
      background: #e5e5e5;
    }
    &:active {
      background: #cecece;
    }
  }
  &.disabled {
    cursor: not-allowed;
    background: #676767;
    color: #cecece;
  }
}
</style>
