export function copyToClipboard(text) {
  const input = document.createElement('input');
  input.value = text;
  const body = document.querySelector('body');
  body.append(input);

  input.select();
  document.execCommand('copy');
  input.remove();
}

const cardNumLimit = {
  pokemon: 4,
  trainer: 4,
  energy: 60,
};
export function getCardNumLimit(cardType) {
  return cardNumLimit[cardType] || 0;
}
