<template lang="pug">
.card
  .img(:style="{height: `${cardHeight}px`, width: `${cardWidth}px`}")
    card-img.card-img(:series="series", :id="id", :height="cardHeight", :width="cardWidth")
    .hover
      text-button(
        v-for="(btn, idx) in buttons"
        :key="idx"
        :disabled="!btn.show"
        :class="{'mr-r-4': idx !== buttons.length - 1}"
        @click="$emit(btn.key)") {{ btn.name }}
      .icon(@click="$emit('remove')") x
  .count(v-if="showCount") {{count}}
  .info(v-if="showInfo") {{series}}-{{idStr}}
  .name(v-if="showName") {{name}}
</template>

<script>
import name from '@/name';
import { paddingToThree } from '@/utils/format.js';
import CardImg from './CardImg';
import { getCardNumLimit } from '@/utils/util.js';

export default {
  name: 'card',
  components: {
    CardImg,
  },
  props: {
    count: {
      type: Number,
      default: 1,
    },
    series: {
      type: String,
      required: true,
    },
    id: {
      type: Number,
      required: true,
    },
    showCount: {
      type: Boolean,
      default: true,
    },
    showName: {
      type: Boolean,
      default: false,
    },
    showInfo: {
      type: Boolean,
      default: false,
    },
    showPrevBtn: {
      type: Boolean,
      default: false,
    },
    showNextBtn: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    cardInfo() {
      this.buttons[0].show = this.count < getCardNumLimit(this.cardInfo.type);
    },
    count() {
      this.checkBtnShow();
    },
    showPrevBtn() {
      this.buttons[2].show = this.showPrevBtn;
    },
    showNextBtn() {
      this.buttons[3].show = this.showNextBtn;
    },
  },
  data() {
    return {
      cardHeight: 225,
      cardWidth: 175,
      buttons: [
        {
          name: '+',
          key: 'add',
          show: true,
        },
        {
          name: '-',
          key: 'minus',
          show: true,
        },
        {
          name: '往前',
          key: 'prev',
          show: true,
        },
        {
          name: '往後',
          key: 'next',
          show: true,
        },
      ],
    };
  },
  computed: {
    name() {
      if (!name[this.series]) {
        return '';
      }
      if (!name[this.series][this.id - 1]) {
        return '';
      }
      return name[this.series][this.id - 1].name;
    },
    idStr() {
      return paddingToThree(this.id);
    },
    cardInfo() {
      return name[this.series][this.id - 1];
    },
  },
  methods: {
    checkBtnShow() {
      this.buttons[0].show = this.count < getCardNumLimit(this.cardInfo.type);
      this.buttons[1].show = this.count > 1;
    },
  },
  mounted() {
    this.checkBtnShow();
    this.buttons[2].show = this.showPrevBtn;
    this.buttons[3].show = this.showNextBtn;
  },
};
</script>

<style lang="scss" scoped>
.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  .count {
    flex: 0 0 40px;
    width: 40px;
    margin-top: -30px;
    font-size: 20px;
    font-weight: bold;
    color: white;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .name {
    margin-top: 8px;
  }
  .info {
    margin-top: 8px;
  }
  .img {
    border-radius: 4px;
    &:hover .hover {
      opacity: 1;
      visibility: visible;
      transition: 0.2s opacity ease-in-out;
      left: 0px;
    }
    .hover {
      border-radius: 4px;
      visibility: none;
      opacity: 0.01;
      transition: 0.2s opacity ease-in-out;
      transform: translateY(-100%);
      background: rgba(0, 0, 0, 0.7);
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      top: -4px;
      left: -500px;
      .icon {
        position: absolute;
        top: 0;
        right: 12px;
        font-size: 32px;
        color: #e74e79;
        user-select: none;
        cursor: pointer;
      }
    }
  }
}
</style>
