<template lang="pug">
#app
  .title-row
    .title PTCG 卡表生成器
    .links(@click="openLINE")
      div.mr-r-4 卡片搜尋機器人
      img(src='./assets/line.png')
  .block
    .title 卡片搜尋
    filters(@add="addCard")
  .block
    .operations
      text-button.mr-r-8(@click="generateCardImg") 生成卡表
      text-button.mr-r-8(@click="generateShareURL") 取得分享網址
      text-button.mr-r-8(@click="cardList = []") 清空卡表
      text-button(@click="sortCard") 自動排序
  .block.card-list
    .title 卡片列表 - 總卡片數 {{ totalCardNum }}
    .list
      card.mr-r-20.mr-b-20(v-for="(card, idx) in cardList",
        :key="card.series + card.id.toString()"
        :count="card.num",
        :series="card.series",
        :id="card.id",
        show-name, show-info
        :show-prev-btn="idx > 0"
        :show-next-btn="idx < cardList.length - 1"
        :data-idx="idx"
        @add="addCardNum(card, 1)"
        @minus="addCardNum(card, -1)"
        @next="changeCard(idx, idx+1)"
        @prev="changeCard(idx-1, idx)"
        @remove="removeCard(idx)")
  .block.ad(v-if="showUnderAd")
    text-button.close-btn(v-if="showCloseAdBtn" @click="showUnderAd = false") 關閉廣告
    InArticleAdsense(
      ins-class="adsbygoogle"
      data-ad-format="fluid"
      data-ad-client="ca-pub-3589478541680972"
      data-ad-slot="2925330139"
    )
  //- .block.ad-right(v-if="showRightAd")
  //-   text-button.close-btn(v-if="showRightAdBtn" @click="showRightAd = false") 關閉廣告
  //-   Adsense(
  //-    data-ad-client="ca-pub-3589478541680972"
  //-    data-ad-slot="1834775590"
  //-    data-ad-format="auto"
  //-    data-full-width-responsive
  //-   )
</template>

<script>
import Card from "@/components/Card.vue";
import Filters from "@/components/Filters.vue";
import TextButton from "@/components/TextButton.vue";
import { cardsToCode, codeToCards } from "@/utils/code";
import { copyToClipboard, getCardNumLimit } from "@/utils/util.js";

// const exportHOST = "http://localhost:5221"
const exportHOST = "https://line-bot.litttlebear.tw"

export default {
  name: "App",
  components: {
    Card,
    Filters,
    TextButton
  },
  data() {
    return {
      cardList: [],
      showUnderAd: true,
      showRightAd: true,
      showCloseAdBtn: false,
      showRightAdBtn: false
    };
  },
  computed: {
    totalCardNum() {
      return this.cardList.reduce((v, card) => (v += card.num), 0);
    }
  },
  methods: {
    openLINE() {
      window.open("https://line.me/R/ti/p/%40295kaagd", "_blank");
    },
    addCard(e) {
      const cardIdx = this.cardList.findIndex(
        x => x.series === e.selected.series && x.id === e.selected.id
      );
      if (cardIdx < 0) {
        this.cardList.push({
          num: e.num,
          ...e.selected
        });
        return;
      }
      this.addCardNum(this.cardList[cardIdx], 1);
      this.$forceUpdate();
    },
    addCardNum(card, num) {
      if (card.num + num <= getCardNumLimit(card.type) && card.num + num > 0) {
        card.num += num;
      } else if (card.num + num === 0) {
        this.removeCard(card);
      } else if (num > 0) {
        card.num = getCardNumLimit(card.type);
      }
      this.$forceUpdate();
    },
    generateCardImg() {
      const code = cardsToCode(this.cardList);

      var request = new XMLHttpRequest();

      function loadData() {
        window.open(this.responseText, "_blank");
      }
      request.addEventListener("load", loadData);
      request.open(
        "POST",
        `${exportHOST}/generate?code=${code}`,
        true
      );
      request.setRequestHeader(
        "Content-Type",
        "application/x-www-form-urlencoded; charset=UTF-8"
      );
      request.send({});
    },
    generateShareURL() {
      const code = cardsToCode(this.cardList);
      const url = new URL(window.location.href);
      const shareURL = url.origin + url.pathname + "?code=" + code;
      copyToClipboard(shareURL);

      alert("分享網址 " + shareURL + " 已經複製");
    },
    changeCard(from, to) {
      if (from < 0 || to < 0) {
        return;
      }
      if (from >= this.cardList.length || to >= this.cardList.length) {
        return;
      }

      [this.cardList[from], this.cardList[to]] = [
        this.cardList[to],
        this.cardList[from]
      ];
      this.$forceUpdate();
    },
    removeCard(idx) {
      this.cardList.splice(idx, 1);
    },
    sortCard() {
      this.cardList.sort((a, b) => {
        const getVal = x => {
          if (x.type === "pokemon") {
            return 0;
          } else if (x.type === "trainer") {
            return 1;
          } else if (x.type === "energy") {
            return 2;
          }
        };
        const diff = getVal(a) - getVal(b);
        return diff === 0 ? (a.id > b.id ? 1 : -1) : diff > 0 ? 1 : -1;
      });
    },
    getMobileOperatingSystem() {
      const userAgent = navigator.userAgent || navigator.vendor || window.opera;

      // Windows Phone must come first because its UA also contains "Android"
      if (/windows phone/i.test(userAgent)) {
        return "Windows Phone";
      }

      if (/android/i.test(userAgent)) {
        return "Android";
      }

      // iOS detection from: http://stackoverflow.com/a/9039885/177710
      if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
        return "iOS";
      }

      return undefined;
    }
  },
  mounted() {
    const that = this;
    setTimeout(() => {
      that.showCloseAdBtn = true;
      that.showRightAdBtn = true;
    }, 3000);
    if (!that.getMobileOperatingSystem()) {
      that.showRightAd = true;
    }

    const url = new URL(document.location.href);
    const code = url.searchParams.get("code");
    if (code === "" || code === undefined || code === null) {
      return;
    }
    that.cardList = codeToCards(code);
  }
};
</script>

